.body{
    overflow: hidden;
}

.steps {
    padding: 10px;
    .step {
        cursor: pointer;
        border-left: 4px solid gray;
        margin: 10px;
        text-align: center;
        padding: 10px;
        box-shadow: 1px 2px 4px 0px #888888;
        transition: box-shadow 0.5s, transform 0.5s, border-left 0.6s;
        border-radius: 10px 10px 10px 10px;
        backface-visibility: hidden;
        h3 {
            margin-bottom: 20px;
            backface-visibility: hidden;
            color: $yellow;
        }
        img {
            transition: 0.6s ease-in-out;
        }
        &:hover {
            box-shadow: 1px 10px 20px rgba(0, 0, 0, 0.2);
            border-left: 4px solid $yellow;
        }
        &:hover img {
            opacity: 0.6;
        }
    }
}

.go_up {
    position: fixed;
    right: 20px;
    bottom: 20px;
    z-index: 25;
    @include respond-to(md) {
        display: none;
    }
}

.scroll-up {
    height: 40px;
    width: 40px;
    position: fixed;
    right: 20px;
    bottom: 20px;
    outline: 1px solid rgba(666, 666, 666, .08);
    cursor: pointer;
    // transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
    svg line {
        stroke-width: 1;
        stroke: #aaa;
        fill: none;
        stroke-dasharray: 20;
        -webkit-transition: all .4s ease;
        transition: all .4s ease;
    }
    &:hover svg line {
        &.top {
            -webkit-transform: translateX(-40px);
            transform: translateX(-40px);
        }
        &.bottom {
            -webkit-transform: translateX(40px);
            transform: translateX(40px);
        }
        &.left {
            -webkit-transform: translateY(40px);
            transform: translateY(40px);
        }
        &.right {
            -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
        }
    }
}

.scroll-up {
    .left-bar {
        position: absolute;
        z-index: 1;
        background-color: transparent;
        top: 19px;
        left: 5px;
        width: 18px;
        display: block;
        transform: rotate(-45deg);
        &:after {
            content: "";
            background-color: #666;
            width: 18px;
            height: 1px;
            display: block;
            border-radius: 1px;
            transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
            transform-origin: right center;
            z-index: -1;
        }
    }
    .right-bar {
        position: absolute;
        z-index: 1;
        background-color: transparent;
        top: 19px;
        left: 17px;
        width: 18px;
        display: block;
        transform: rotate(45deg);
        border-radius: 2px;
        &:after {
            content: "";
            background-color: #666;
            width: 18px;
            height: 1px;
            display: block;
            border-radius: 1px;
            transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
            transform-origin: left center;
            z-index: -1;
        }
    }
    &:hover {
        transition: all .1s;
        .left-bar:after {
            transform: rotate(-10deg);
        }
        .right-bar:after {
            transform: rotate(10deg);
        }
    }
}

.heading__block {
    .row {
        margin-bottom: 50px;
    }
    margin-bottom: 30px;
    .section__heading {
        margin-bottom: 25px;
        position: relative;
        font-weight: 700;
        color: #D0BB57;
        font-size: 45px;
    }
    .section__heading:after {
        margin: 0 20px 8px 0;
        height: 3px;
        content: " ";
        text-shadow: none;
        background-color: #D0BB57;
        width: 200%;
        position: absolute;
        left: -20px;
        top: 40%;
        transform: translate(-100%, 50%);
    }
    .section__paragraph {
        //margin-bottom: 50px;
        font-size: 18px;
    }
    h3{
        color: $yellow;
    }
}
.heading__block div.image__block_row{
    @include respond-to(md){
        margin-bottom: 0;
    }
}
.flex__block {
    display: flex;
    @include respond-to(md){
        display: block;
    }
    .desktop__image_block{
        @include respond-to(md){
            display: none;
        }
    }
    .mobile__image_block{
        display: none;
        height: 150px;
        @include respond-to(md){
            display: block;

        }
    }
}

.image__block {
    border-radius: 10px;
}


.partners__block a img{
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
    opacity: 0.3;
    -webkit-transition: 0.6s ease-in-out;
    -moz-transition: 0.6s ease-in-out;
    -ms-transition: 0.6s ease-in-out;
    -o-transition: 0.6s ease-in-out;
    transition: 0.6s ease-in-out;

    &:hover{
        -webkit-filter: grayscale(0);
        filter: grayscale(0);
        opacity: 1;
    }
}
.sl-slider-wrapper{
    z-index:26;
}